.all {
  width: 230px;
}

.all p,
.all h5 {
  width: fit-content;
  text-align: center;
  margin: auto;

  font-style: italic;
}

.all p {
  font-size: 13px;
  opacity: 0.7;
  margin-top: 4px;
}

.all img:hover {
  opacity: 0.8;
}

.all img,
.all p,
.all h5 {
  cursor: pointer;
}
